import React from 'react';
import { withRouter, Link } from "react-router-dom";
import { Typography, Button } from "@material-ui/core";



const Postdetails = ({ posts, match:{params: {id }}, deletePost, history }) => {

    const findPost = posts.find(post => post.id === Number(id));
    const handleDelete = (id) => {
        deletePost(id);
        history.push('/');
    }
    return ( 
        <>
        <Typography variant='h5' component='h3'>
            {findPost.title}
        </Typography>
        <img src={`/img/${findPost.img_url}`} alt='my blog pic' />
        <Typography variant="body2">
            {findPost.body}
        </Typography>
        <Typography variant='caption' display='block' align='right'>
            Writtern by Sayed
        </Typography>
        <Button component={Link} to={`/edit/${findPost.id}`} variant='contained' color='primary'>
            Edit
        </Button>        
        <Button onClick={() => handleDelete(findPost.id)} variant='contained' color='primary'>
            Delete
        </Button>
        </>
        );
}
 
export default withRouter(Postdetails);